<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Hasil Ujian</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Filter Kelas</ion-label>
          <ion-select v-model="selectedClass" @ionChange="filterResults">
            <ion-select-option value="">Semua Kelas</ion-select-option>
            <ion-select-option
              v-for="kelas in classes"
              :key="kelas.id"
              :value="kelas.name"
            >
              {{ kelas.name }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label>Filter Mata Pelajaran</ion-label>
          <ion-select v-model="selectedSubject" @ionChange="filterResults">
            <ion-select-option value="">Semua Mata Pelajaran</ion-select-option>
            <ion-select-option
              v-for="subject in subjects"
              :key="subject.id"
              :value="subject.name"
            >
              {{ subject.name }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-button style="color: white" expand="block" @click="exportToExcel">
          Ekspor ke Excel
        </ion-button>
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Hasil Ujian</h2>
              <ion-list>
                <ion-item>
                  <ion-label>
                    <table>
                      <thead>
                        <tr>
                          <th>Nama Siswa</th>
                          <th>Kelas</th>
                          <th>Mata Pelajaran</th>
                          <th>Nilai</th>
                          <th>Tanggal</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="result in filteredExamResults"
                          :key="result.id"
                        >
                          <td>{{ result.userName }}</td>
                          <td>{{ result.kelasName }}</td>
                          <td>{{ result.mataPelajaranName }}</td>
                          <td>
                            <ion-badge
                              :color="result.score < 65 ? 'danger' : 'success'"
                            >
                              {{ result.score }}
                            </ion-badge>
                          </td>
                          <td>{{ formatDate(result.createdAt) }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </ion-label>
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonItem,
  IonLabel,
  IonToast,
  IonBadge,
  IonSelect,
  IonSelectOption,
  IonButton,
} from "@ionic/vue";
import { ref, onMounted, computed } from "vue";
import { getFirestore, collection, getDocs } from "firebase/firestore";
import { firebaseApp } from "../firebase.js";
import * as XLSX from "xlsx";

const db = getFirestore(firebaseApp);
const examResults = ref([]);
const classes = ref([]);
const subjects = ref([]);
const selectedClass = ref("");
const selectedSubject = ref("");
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchAllExamResults = async () => {
  try {
    const resultsSnapshot = await getDocs(collection(db, "nilaiUjian"));
    const results = resultsSnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));

    examResults.value = results;
    console.log("Hasil ujian:", examResults.value); // Debugging log
  } catch (error) {
    console.error("Error fetching exam results:", error);
    showToast("Terjadi kesalahan saat mengambil data hasil ujian.", "danger");
  }
};

const fetchClasses = async () => {
  const querySnapshot = await getDocs(collection(db, "kelas"));
  classes.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const fetchSubjects = async () => {
  const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
  subjects.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const filteredExamResults = computed(() => {
  return examResults.value.filter((result) => {
    const classMatch =
      !selectedClass.value || result.kelasName === selectedClass.value;
    const subjectMatch =
      !selectedSubject.value ||
      result.mataPelajaranName === selectedSubject.value;
    return classMatch && subjectMatch;
  });
});

const filterResults = () => {
  console.log("Filter applied");
};

const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = new Date(timestamp.seconds * 1000);
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const exportToExcel = () => {
  const worksheet = XLSX.utils.json_to_sheet(filteredExamResults.value);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Hasil Ujian");
  XLSX.writeFile(workbook, "hasil_ujian.xlsx");
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchAllExamResults();
    event.target.complete();
  }, 2000);
};

onMounted(() => {
  fetchAllExamResults();
  fetchClasses();
  fetchSubjects();
});
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
